
:root {
  // == PRIMARY:
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --background: hsl(0, 0%, 95%);

  // == NEUTRAL:
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --white: hsl(0, 0%, 100%);


  --shadow: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.0001) 100%);
  --proba: hsl(0, 0%, 95%);
}

















